<template>
    <transition-group name="slide" tag="div" class="ui-toast-list component">
        <UiToast
            @remove="onRemove"
            v-for="item in toastsList"
            :item="item"
            :key="item.id"
        />
    </transition-group>
</template>

<script>
import UiToast from './UiToast';
export default {
    name: 'UiToastList',

    components: {
        UiToast
    },

    watch: {
        $route: {
            handler: 'clearToastList',
            deep: 'true'
        }
    },

    computed: {
        toastsList() {
            return this.$store.state.toast.toastsList;
        }
    },

    methods: {
        onRemove(id) {
            this.$store.commit('toast/REMOVE', { id });
        },
        clearToastList() {
            if (this.toastsList.length) {
                this.$store.commit('toast/CLEAR_LIST');
            }
        }
    }
};
</script>

<style lang="scss">
    .ui-toast-list.component {
        @apply top-6 w-full fixed z-50 grid place-content-center
    }

    .slide-leave-active,
    .slide-enter-active {
        transition: 0.3s;
    }
    .slide-enter {
        transform: translate(0, 100%);
    }
    .slide-leave-to {
        transform: translate(0, -120%);
    }


    .ui-toast.component {
        @apply bg-grays-50 rounded cursor-pointer text-center p-3 my-3 w-80;
        &.default {
            @apply border-l-4  bg-grays-200;
        }
        &.success {
            @apply bg-alert-success;
        }
        &.info {
            @apply bg-alert-info;
        }
        &.warning {
           @apply bg-alert-warning;
        }
        &.error {
            @apply bg-alert-error;
        }
    }
</style>